* {
  box-sizing: border-box;
}

body {
  --type-first: Helvetica, Arial, sans-serif;
  --type-second: 'Spectral', Georgia;

  font-family: var(--type-first);
  color: #333;
  padding-top: 4rem;
  margin: 0;
}

h1, h2, h3, h4, p {
  margin: 0;
}

ul, li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
}

button, input {
  display: block;
  font-size: 1rem;
  font-family: var(--type-first);
  color: #333;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  max-width: 50rem;
  padding: 0 1rem;
  margin: 0 auto;
}

.mainContainer {
  margin-top: 2rem;
}

.title {
  position: relative;
  font-family: var(--type-second);
  font-size: 3rem;
  line-height: 1;
  margin: 1rem 0;
  z-index: 1;
}

.title::after {
  content: '';
  position: absolute;
  width: 1.5rem;
  height: 1.5rem;
  display: block;
  background: #fb1;
  border-radius: 0.5rem;
  bottom: 5px;
  left: -5px;
  z-index: -1;
}

.animeLeft {
  opacity: 0;
  transform: translateX(-20px);
  animation: animeLeft .3s forwards;
}

@keyframes animeLeft {
  to {
    opacitY: 1;
    transform: none;
    transform: initial;
  }
}

.App {
  min-height: calc(100vh + 10rem);
  display: flex;
  flex-direction: column;
}

.AppBody {
  flex: 1 1;
}

.VictoryContainer {
  height: auto !important;
  height: initial !important;
}
